// modal
.modal {
  display: none;
  background-color: rgba(63,68,85,0.85);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  display: none;
  width: 100%;
  height: 100%;
  outline: 0;
  overflow-x: hidden;
  overflow-y: auto;
  & a {
    color: #3b85ea;
    font-size: 14px; }
  &__dialog {
    min-height: calc(100% - (1.75rem * 2));
    display: flex;
    align-items: center;
    position: relative;
    width: auto;
    margin: .5rem;
    pointer-events: none;
    margin: 1.75rem auto;
    &--small {
      max-width: 400px;
      & .modal__body {
        padding: 25px 15px; } }
    &--medium {
      max-width: 620px;
      & .modal__body {
        padding: 20px;
        @include rmin(768) {
          padding: 40px; } } }
    &--invest {
      max-width: 480px; } }
  &__content {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    outline: 0;
    padding: 10px; }

  &__body {
    background-color: #fefefe;
    border-radius: 4px;
    & p {
      font-size: 13px;
      font-family: 'Lato';
      margin-bottom: 5px;
      &.descr {
        margin-bottom: 20px; } }
    & hr {
      margin-top: 20px;
      margin-bottom: 20px;
      background: #758293;
      color: #758293; }
    & ol {
      margin-bottom: 30px;
      list-style: decimal;
      margin-left: 20px;
      & li {
        color: #758293;
        font-family: 'Lato';
        font-size: 16px;
        margin-bottom: 20px; } } }
  &__btitle {
    font-family: 'Lato';
    color: #758293;
    font-size: 22px;
    margin-bottom: 10px; }
  &__header {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between; }
  &__htitle {
    font-family: 'Russo One', sans-serif;
    font-weight: 400;
    color: #ffffff;
    font-size: 22px;
    padding-right: 20px; }
  &__close {
    cursor: pointer;
    margin-top: 3px;
    & svg {
      fill: #ffffff;
      width: 20px;
      height: 20px; } }

  &__btn {
    color: #fff;
    border-radius: 3px;
    background: #2cae82;
    padding: 13px 25px;
    width: 100%;
    &--blue {
      background-color: #4088ea; }
    &--auto {
      width: 100%;
      @include rmin(768) {
        width: auto; } } }
  &__oauth {
    margin-top: 20px;
    text-align: center; }

  // invest btn
  &__invest {
    display: flex;
    justify-content: space-between;
    &-btn {
      display: block;
      text-decoration: none;
      width: calc(50% - 0.5rem);
      background-color: #fefefe;
      border-radius: 4px;
      padding: 20px;
      font-size: 22px;
      font-family: 'Russo One', sans-serif;
      font-weight: 400;
      color: #05203f;
      & img {
        margin-bottom: 5px; } } }

  // modal body style
  &__title {
    color: #05203f;
    font-size: 25px;
    font-family: 'Lato';
    margin-bottom: 30px;
    &--flag {
      margin-bottom: 20px;
      padding-left: 30px;
      position: relative;
      &:before {
        content: ' ';
        position: absolute;
        top: 5px;
        left: 0;
        background-image: url(img/modal/flag.png);
        background-size: contain;
        background-repeat: no-repeat;
        width: 25px;
        height: 35px;
        display: block; } } }

  &__subtitle, &__subtitle a {
    color: #05203f;
    font-size: 17px;
