  &__footer {
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center; }
  &__square {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 10px;
    &-item {
      width: 100%;
      min-width: 120px;
      padding: 20px 15px;
      text-align: center;
      box-shadow: rgba(1,1,1,.05) 0 0 10px;
      text-decoration: none;
      border: 1px solid #eeeeee;
      margin-bottom: 20px;
      @include tr(all ease 0.2s);
