  &__deadline {
    font-size: 16px !important;
    margin-bottom: 30px !important; }

  // radio
  &__radio-group {
    margin-bottom: 5px; }
  &__radio-select {
    margin-bottom: 30px;
    display: flex;
    flex-wrap: wrap;
    .radio {
      position: absolute;
      z-index: -1;
      opacity: 0;
      margin: 10px 0 0 7px;
      + label {
        position: relative;
        padding: 0 25px 0 20px;
        cursor: pointer;
        font-size: 13px;
        color: #758293;
        &:before {
          content: '';
          position: absolute;
          top: 1px;
          left: 0;
          width: 12px;
          height: 12px;
          background: url('img/radio.png'); } }
      &:checked + label:before {
        background: url('img/radio-active.png'); } } }

  // checkbox
  .checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0;
    margin: 10px 0 0 7px;
    + label {
      position: relative;
      padding: 0 0 10px 20px;
      cursor: pointer;
      font-size: 13px;
      color: #758293;
      display: block;
      font-weight: 400;
      font-family: 'Lato';
      &:before {
        content: '';
        position: absolute;
        top: 1px;
        left: 0;
        width: 12px;
        height: 12px;
        background: url('img/checkbox.png');
        border-radius: 4px; } }
    &:checked + label:before {
      background: #758293; } }

